  --text-white-color: #fff;
  --text-black-color: #000;
  --text-grey-color: #907e82;
  --button-hover-color: #ecebeb;
  --icons-bg-color: #fea5bb;
  --border-set: border-bottom: 1px solid #e1e1e1;
  --btn-trans-duration: 250ms;
  --btn-trans-timing: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
